<!-- 메인 페이지 마크업 -->
<template lang="html">
  <div class="box1">
    <div>
    <div v-show="message">{{ msgtext }}</div>

    <ul>
      <li v-for="(group, index) in groups" :key="group" @mouseover="hovered = index" @mouseleave="hovered = null">
        <button @click="grouping(index)">
          <div class="imgBox">
            <img :src="hovered === index ? require('../assets/icon/icon_jong' + (index + 1) + 'on.png') : require('../assets/icon/icon_jong' + (index + 1) + '.png')" alt="group" />
          </div>
          <span> {{ group }}</span>
        </button>
      </li>
    </ul>
  </div>
    <div>
      <label for="title">지역 </label>
      <input id="title" type="text" v-model="value" />
      <button @click="search()">검색</button>
    </div>
    
    <div v-show="message">{{ msgtext }}</div>
    <div class="speciesList">
      <div v-for="x in species" :key="x.id" class="speciesBox">
        <img :src="require('../assets/species/' + x.이미지)" alt="" />
        <h4>
          {{ x.NO + "." + x.대표국명 }} <span>{{ "(" + x.Familyko + ")" }}</span>
        </h4>
        <p>{{ x.학명 }}</p>
        <p>{{ "멸종위기 " + x.등급 + " 급 " + x.관리분류군 }}</p>
        <p>{{ x.대표국명 }}</p>
      </div>
    </div>
  </div>
</template>

<script>
import jdata from "../species.json";

export default {
  
  data() {
    return {
      message: false,
      msgtext: "검색 데이터가 없습니다",
      value: "",
      species: jdata,
      hovered: null,
      groups: ["포유류", "조류", "파충류", "양서류", "어류", "곤충류", "무척추동물", "육상식물", "해조식물", "고등균류"],
      species2: jdata,

    };
  },
  methods: {
    search: function () {
      let text = this.value;
      if (text) {
        this.species = jdata.filter(function (element) {
          return element.대표국명.includes(text);
        });
        this.message = true;
        this.msgtext = this.species.length == 0 ? "검색 데이터가 없습니다" : this.species.length + "개가 검색되었습니다";
      } else {
        alert("검색어를 입력해주세요!");
      }
    },
    grouping: function (index) {
      let text2 = this.groups[index];
        console.log(text2)
      if (text2) {
        this.species = jdata.filter(function (element) {
          return element.분류군.includes(text2);
        });
        this.message = true;
        this.msgtext = this.species.length == 0 ? "검색 데이터가 없습니다" : this.species.length + "개가 검색되었습니다";

      } else {
        alert("검색어를 입력해주세요!");
      }
    },

  },
};
</script>

<style>
.box1 {
  width: 90%;
  margin: 10px 5%;
  padding: 20px;
  box-sizing: border-box;
  background: #fff;
  color: #000;
}
.speciesList {
  display: flex;
  flex-wrap: wrap;
}

.speciesBox {
  flex-basis: 20%;
  margin: 2.5%;
  overflow: hidden;
  width: 285px;
  height: 370px;
  background: #999;
}
.speciesBox img {
  width: 100%;
  height: 252px;
  object-fit: cover;
}
</style>
<style scoped>
button {
  background: transparent;
  border: none;
}
ul,
li {
  list-style: none;
  margin: 10px;
}
ul {
  display: flex;
  justify-content: space-around;
}
li {
  transition: 0.3s;
  width: 9%;
  height: 6.95rem;
  position: relative;
  margin-left: 1%;
  display: flex;
  align-items: center;
}
span {
  margin-top: 20px;
  display: block;
  text-align: center;
}
img {
  transition: all 0.6s ease-out;
}
.imgBox {
  transition: all 0.3s ease-out;
  height: 7rem;
  width: 7rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid #2f7ccd;
  border-radius: 50% 50%;
}
li:hover .imgBox {
  background: linear-gradient(to bottom, #08e4e7, #2f7ccd);
  transition: all 0.3s ease-out;
}
img {
}
li,
li:hover {
  transition: all 0.5s ease;
}

